@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Assistant&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-primary: #397f6e;
  --color-primary-transparent: rgba(57, 127, 110, 0.2);
  --color-text: #000000;
  --color-disabled: #d9d9d9;
  --color-lightgrey: #f6f7f9;
  --color-lightgreen: #ebf2f1;
  --color-white: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Assistant", sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Kumbh Sans', sans-serif;
}

.hover {
  transition: box-shadow 0.1s ease-in-out;
  border-radius: .5rem;
}

.hover:hover {
  box-shadow: 0 0 0 0.33rem rgba(57, 127, 110, 0.2);
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
header {
  height: 5em;
  padding: 1rem;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav,ul {
  height: 100%;
}

ul {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

ul > li > a {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul > li a {
  font-size: 17px;
  color: var(--color-text);
  text-decoration: none;
  padding: 0.5em;
}

nav > div {
  display: flex;
  grid-gap: .5rem;
  gap: .5rem;
  align-items: center;
}

nav > div > div {
  display: flex;
  align-items: center;
  grid-gap: .5rem;
  gap: .5rem;
}
.btn {
  padding: 12.75px 25.5px;
  border-radius: 3.4px;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  font-size: 17px;
  display: inline-flex;
  transition: all .1s ease-in-out;
}

.btn.small {
  padding: 0.5rem 1rem;
}

.btn.full {
  background-color: var(--color-primary);
  color: white;
}

.btn:hover {
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
